

















































	var view;
	export default {
		data() {
			return {
				index: 0,
				query: '',
				noticeCategoryId: '',
				noticeCategoryName: '',
				swiper: [{
						page: 1,
						list: [],
						loadingType: 0,
						isRead: 'N'
					},
					{
						page: 1,
						list: [],
						loadingType: 0,
						isRead: 'Y'
					}
				],
				swipe2Loaded: false,
			};
		},
		onPullDownRefresh() {
			let item = view.swiper[view.index];
			item.page = 1;
			item.loadingType = 0;
			getNotice();
		},
		onShow() {
			let param = uni.getStorageSync('param');
			if (param) {
				if (this.noticeCategoryId != param.id) {
					this.noticeCategoryId = param.id;
					this.noticeCategoryName = param.categoryName;
					getNotice();
				}
				uni.removeStorageSync('param');
			}
		},
		onLoad() {
			view = this;

			getNotice();
		},
		methods: {
			swiperChange(e) {
				console.log(e);
				this.onTabChange(e.detail.current);
			},
			onTabChange(index) {
				this.index = index;
				if (index == 1 && !this.swipe2Loaded) {
					this.swipe2Loaded = true;
					getNotice();
				}
			},
			onItemClick(item) {
				if (item.noticeAccessoryList.length > 0) {
					uni.showActionSheet({
						itemList: item.noticeAccessoryList.map(item => item.accessoryName),
						success(index) {
							console.log(index);
							downloadFile(item, index.tapIndex);
						}
					})
				} else {
					this.toast("该通知没有附件可以查看");
				}

			},
			scrolltolower() {
				let item = view.swiper[view.index];
				if (item.loadingType == 0) {
					item.page++;
					item.loadingType = 1;
					getNotice();
				}
			},
			onSearch(query) {
				let item = view.swiper[view.index];
				this.query = query;
				item.page = 1;
				uni.showLoading({
					title: '正在搜索',
				})
				getNotice();
			}
		},
	}

	function downloadFile(item, index) {
		uni.showLoading({
			title: '正在下载',
		})

		console.log(JSON.stringify(item));
		let accessoryUrl = item.noticeAccessoryList[index].accessoryUrl.replace(/\\/g, '/');

		uni.downloadFile({
			url: view.BASE_URL + accessoryUrl,
			success(res) {
				console.log(JSON.stringify(res));
				uni.hideLoading();
				if (res.statusCode == 200) {
					var filePath = res.tempFilePath;
					//saveFile(filePath, item, index);
					openFile(filePath, item, index);
				} else {
					view.toast("下载失败，没有找到要下载的资源");
				}
			},
			fail(e) {
				uni.hideLoading();
				console.log(JSON.stringify(e));
				view.toast("下载失败");
			},
		})
	}

	function saveFile(filePath, item, index) {

		uni.saveFile({
			tempFilePath: filePath,
			success: function(res) {

				console.log(JSON.stringify(res));
				var savedFilePath = res.savedFilePath;

				openFile(savedFilePath, item, index)
			},
			fail(e) {
				console.log(JSON.stringify(e));
				view.toast('保存文件失败');
			}
		});
	}

	function openFile(filePath, item, index) {

		uni.openDocument({
			filePath: filePath,
			success: function(res) {
				console.log(JSON.stringify(res));
				console.log('打开文档成功');
				console.log(item.noticeAccessoryList.length === index + 1);
				if (item.noticeAccessoryList.length === index + 1) {
					changeRead(item);
				}
			},
			fail(e) {
				console.log(JSON.stringify(e));
				console.log('打开文档失败');
				view.toast('打开文档失败');
			}
		});
	}

	function changeRead(item) {

		view.$request({
			url: 'saveReadNotice:' + item.id,
			method: 'POST',
			data: {},
			success(res) {
				if (res.status == true) {
					console.log(JSON.stringify(res));
				}
			},
		})
	}


	function getNotice() {

		let item = view.swiper[view.index];

		view.$request({
			url: 'listNotice',
			data: {
				query: view.query,
				noticeCategoryId: view.noticeCategoryId,
				isRead: item.isRead,
				page: item.page,
				rows: '10',
			},
			success(res) {
				if (res.status == true) {
					res.data.forEach(item => {
						if (item.releaseTime) {
							item.releaseTime = item.releaseTime.split(' ')[0];
						}
					})
					if (item.page == 1) {
						item.list = res.data;
					} else {
						item.list.push(...res.data);
					}

					if (res.data.length == 10) {
						item.loadingType = 0;
					} else {
						item.loadingType = 2;
					}
				}
			},
			complete() {
				uni.hideLoading();
				uni.stopPullDownRefresh();
			},
		})
	}
